body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  text-align: center;
}

.header {
  background-color: #5aa3ff;
  padding-top: 40px;
  padding-bottom: 30px;
}

.catEmoji {
  font-size: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.title {
  padding-top: 0px;
  padding-bottom: 5px;
  font-size: 37px;
  font-weight: 600;
  letter-spacing: -1px;
  text-align: center;
  color: #ffffff;
}

.subTitle {
  padding-bottom: 12px;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: -0.5px;
  text-align: center;
  color: #ffffff;
}

.smallText {
  font-size: 9px;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: -0.5px;
  text-align: center;
  color: #ffffff;
}

.catInfo {
  width: 320px;
  margin: auto;
  padding-top: 35px;
}

.row {
  height: 37px;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 18px;
}

label {
  font-size: 15px;
  font-weight: 600;
  letter-spacing: -0.5px;
  color: #626262;
}

.inputStyle {
  height: 37px;
  margin-left: 15px;
  padding-left: 14px;
  padding-right: 14px;
  font-size: 14px;
  letter-spacing: -0.5px;
  background-color: #fff;
  color: #626262;
  border-radius: 10px;
  border: solid 1px #efefef;
  box-shadow: 0 0 0 1px #3991ff;
  -webkit-transition: all 100ms;
  transition: all 100ms;
  -webkit-box-shadow: none;
}

.inputStyle:focus {
  border: solid 1px #3991ff;
}

.inputStyle:hover {
  border: solid 1px #b3b3b3;
}

.warnInputStyle {
  height: 37px;
  margin-left: 15px;
  padding-left: 14px;
  padding-right: 14px;
  font-size: 14px;
  letter-spacing: -0.5px;
  background-color: #fff;
  color: #626262;
  border-radius: 10px;
  border: solid 1px #ff822f;
  box-shadow: 0 0 0 1px #3991ff;
  -webkit-transition: all 100ms;
  transition: all 100ms;
  -webkit-box-shadow: none;
}

input:focus {
  outline: none;
}

::-webkit-input-placeholder {
  font-size: 14px;
  letter-spacing: -0.5px;
  color: #d4d4d4;
}

::-ms-input-placeholder {
  font-size: 14px;
  letter-spacing: -0.5px;
  color: #d4d4d4;
}

::placeholder {
  font-size: 14px;
  letter-spacing: -0.5px;
  color: #d4d4d4;
}

.catName {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.weightAndAge {
  width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.catWeight input {
  width: 70px;
}

.alignRight ::-webkit-input-placeholder {
  text-align: right;
}

.alignRight ::-ms-input-placeholder {
  text-align: right;
}

.alignRight ::placeholder {
  text-align: right;
}

.catAge input {
  width: 70px;
}

.catStatus {
  width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.selectBox {
  margin-left: 20px;
}

.mealKcal {
  width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.mealKcal input {
  width: 146px;
}

.mealKcal ::-webkit-input-placeholder {
  text-align: right;
}

.mealKcal ::-ms-input-placeholder {
  text-align: right;
}

.mealKcal ::placeholder {
  text-align: right;
}

.mealKcal span {
  font-size: 15px;
  font-weight: 400;
  letter-spacing: -0.5px;
  color: #626262;
}

.submitButton {
  text-align: center;
}

button {
  width: 120px;
  height: 32px;
  border-radius: 16px;
  border-color: #3991ff;
  background-color: #3991ff;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.5px;
  color: #ffffff;
  margin-top: 15px;
}

button:focus {
  outline: none;
}

.invisible {
  display: none;
}

.visible {
  height: 125px;
  margin-top: 40px;
  display: block;
  text-align: center;
  font-size: 23px;
  letter-spacing: -0.7px;
  color: #4d4d4d;
}

.visible div {
  margin-top: 35px;
  margin-bottom: 35px;
}

.visible hr {
  width: 280px;
  border: solid 0.5px #ededed;
  margin: auto;
}

.visible span {
  font-size: 23px;
  font-weight: bold;
  letter-spacing: -0.7px;
  color: #3991ff;
}

.infoTitle {
  font-size: 13px;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: -0.5px;
  text-align: center;
  color: #949494;
}

.info {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.5px;
  text-align: center;
  color: #949494;
}

